import React from 'react';

import Info from './info';
import BookHistory from './bookHistory';
import './style.css'
export default class BooksInfoPageIndex extends React.Component {


constructor(){
		super();
		this.state = {
            id:null,
        	student:null,
        	studentHistory:[],
        	books:[]
		};
		
	}

fetchStudentbyID=(id)=>{
if(id)

fetch(`https://stark-hamlet-65683.herokuapp.com/studentinfo/${id}`,{
      method: 'get',
      headers: {'Content-Type': 'application/json'}
  }).then(response=>response.json()).then(data=>{if(data)this.setState({student:data[0]})});
else
	window.location.href = window.location.origin+"/students";


}
fetchStudentFromHistory=(id)=>{

if(id)

fetch(`https://stark-hamlet-65683.herokuapp.com/issuereturnstd/${id}`,{
      method: 'get',
      headers: {'Content-Type': 'application/json'}
  }).then(response=>response.json()).then(data=>{if(data)this.setState({studentHistory:data})});

}

fetchAllBooks=()=>{
fetch('https://stark-hamlet-65683.herokuapp.com/book', {
      method: 'get',
      headers: {'Content-Type': 'application/json'}
  }).then(response=>response.json()).then(data=>{if(data)this.setState({books:data})})

}


componentDidMount(){
let id=window.location.pathname;
id=id.substring(id.lastIndexOf('/')+1);
this.setState({id});
this.fetchStudentbyID(id);
this.fetchStudentFromHistory(id);
this.fetchAllBooks();
}


render(){

	const {student,studentHistory,books}=this.state;
	return(
				<div className="pageView">
							{student && books.length>0?
						<div className="pageRow">
											
								<Info student={student}/>
								<BookHistory studentHistory={studentHistory} books={books}/>

						</div>
										 :


										 <div  className="pageRow"><div className="load">Ooops ! We dont have any Student with that ID</div></div>


							}




				</div>








		 )






}



}//class closed